module.exports ={ 
	state: {
		account: '',
		pwd: ''
	},
	mutations: {
		login(state, payload) {
			sessionStorage.setItem('account',payload.account);
			sessionStorage.setItem('pwd',payload.pwd);
			state.account = payload.account;
			state.pwd = payload.pwd;
		},
		getState(state){
			state.account = sessionStorage.getItem('account');
			state.pwd = sessionStorage.getItem('pwd');
		},
		exit(state) {
			sessionStorage.removeItem('account');
			sessionStorage.removeItem('pwd');
			state.account = '';
			state.pwd = '';
		}
	},
	actions: {}
}
